  overflow: hidden !important;
  /**  **/
  word-break: break-all;
  text-overflow: ellipsis !important;
  /**  **/
  display: -webkit-box !important;
  /**  **/
  -webkit-box-orient: vertical;
  /**  **/
  -webkit-line-clamp: 2;
  /**  **/
  line-height: 20px;
}
.fd-line-clamp.other-line-clamp {
  position: relative;
  display: block !important;
  font-size: 16px;
}
.fd-line-clamp.other-line-clamp::before {
  content: "";
  float: left;
  width: 30px;
  height: 100%;
}
.fd-line-clamp.other-line-clamp .fd-line-clamp-text {
  position: relative;
  z-index: 1;
  float: right;
  margin-left: -30px;
  width: 100%;
